body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: "Comic Sans MS";
  height: 100%;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 15s linear;
          animation: App-logo-spin infinite 15s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.social-media {
  text-align: justify;
  opacity: 0;
  background: lightyellow;
  position: absolute;
  top: 0;
  right: 0;
  width: 20em;
  padding: 1em 1em 2em;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  z-index: 2;
}

.social-media:hover {
  opacity: 1;
}

.content {
  position: absolute;
  top: 0;
  width: 100%;
}

.background {
  opacity: 0.4;
  background-color: #000;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.foreground {
  opacity: 0;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

@media only screen and (max-width: 600px) {
  iframe {
    top: -590px;
    left: -440px;
    width: 300%;
    height: 300%;
  }
}
@media only screen and (min-width: 600px) {
  iframe {
    top: -590px;
    left: -440px;
    width: 300%;
    height: 300%;
  }
}
@media only screen and (min-width: 768px) {
  iframe {
    top: -300px;
    left: -190px;
    width: 180%;
    height: 180%;
  }
}
@media only screen and (min-width: 992px) {
  iframe {
    top: -230px;
    left: -190px;
    width: 150%;
    height: 150%;
  }
}
@media only screen and (min-width: 1200px) {
  iframe {
    top: -230px;
    left: -340px;
    width: 150%;
    height: 150%;
  }
}

iframe {
  overflow: hidden;
  /* top: -230px; */
  /* left: -340px; */
  /* width: 150%; */
  /* height : 150%; */
  position: fixed;
}

.video-wrapper {
  position: static;
}

.version {
  color: white;
  position: absolute;
  bottom: 0;
  right: 0;
}

